import React, { Component } from 'react';
import Waiter from './Waiter';
import Auth from './Auth';
import Map from './Map';
import Interface from './Interface';

import requestData from '../actions/requestData.js';
import convertData from '../actions/convertData.js';
import filterData from '../actions/filterData.js';

import branches from '../testdata/branches.json';
import applications from '../testdata/applications.json';

export default class App extends Component {
  state = {
    authRequired: true,
    error: false,
    waiting: false,
    data: null,
    filteredData: null,
  };

  componentDidMount() {
    // (async () => {
    //   let data = await convertData({branches, applications});
    //   console.log(data);
    //   this.setState({ data, filteredData: data });
    // })();

    this.checkAccess();
  }

  exit = () => {
    this.setState({
      authRequired: true,
      data: null,
      filteredData: null,
    });
  };

  onAuthSuccess(user) {
    this.getData(user);
  }

  async checkAccess() {
    this.setState({ waiting: true });
    const res = await requestData('');
    if (res.status === 404) {
      this.onAuthSuccess();
    } else if (res.status === 401) {
      this.setState({ waiting: false });
    } else {
      this.onError(res);
    }
  }

  async getData(user) {
    user = Object.assign({}, user);
    this.setState({ waiting: true });

    const responses = {
      branches: await requestData('/catalogs/12/records', user),
      applications: await requestData('/catalogs/11/records', user),
    };

    for (let key in responses) {
      if (responses[key].status === 200) responses[key] = await responses[key].json();
      else {
        this.onError(responses[key]);
        return;
      }
    }

    const data = await convertData(responses, user);

    this.setState({ authRequired: false, data, filteredData: data, waiting: false });
    console.log('data:', this.state.data);
  }

  onError(res) {
    this.setState({ error: res });
  }

  filterData = (filter) => {
    this.setState({
      filteredData: filterData(this.state.data, filter),
    }, () => console.log('filtered data:', this.state.filteredData));
  };

  render() {
    const auth = !this.state.waiting && this.state.authRequired ? <Auth key="auth" onAuthSuccess={(user) => this.onAuthSuccess(user)}/> : null;
    const waiter = this.state.waiting ? <Waiter key="waiter"/> : null;
    const map = <Map key="map" data={this.state.filteredData}/>;
    const ui = this.state.filteredData ? <Interface key="interface" data={this.state.filteredData} filterData={this.filterData} exit={this.exit}/> : null;

    let content = [waiter, auth, ui, map];
    if (this.state.error) content = <Err res={this.state.error}/>;

    return content;
  }
}

const Err = (props) => {
  return (
    <div>{`${props.res.status} ${props.res.statusText}`}</div>
  );
};
